/* AssignmentLinkSection.css */
section {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 8px;
}

input {
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  box-sizing: border-box;
}

button {
  background-color: blue;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
/* AssignmentLinkSection.css */

.submission-message {
  font-size: 24px;
  font-weight: bold;
  color: green; /* You can choose your preferred color */
  margin-top: 20px;
}
